@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.save-search-btn {
  display: flex;
  align-items: center;
  color: pick-color('secondary', 'blue');
  padding: 0;
  position: relative;

  .btn-span {
    font-size: 14px;
    line-height: 20px;
    padding: 6px 16px;
    transition: all 0.2s;
    border: 2px solid pick-color('secondary', 'blue');
    border-right: 0;
    border-radius: 6px 0 0 6px;

    &:not(&.btn-disabled):hover {
      background-color: pick-color('secondary', 'blue');
      color: white;
    }
  }

  mat-icon {
    transition: transform 0.2s;

    &.expanded {
      transform: rotate(-180deg);
    }
  }

  .dropdown-toggle-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 6px 10px;
    border: 2px solid pick-color('secondary', 'blue');
    border-radius: 0 6px 6px 0;
    transition: all 0.2s;

    &:hover {
      background-color: pick-color('secondary', 'blue');
      color: white;
    }

    mat-icon {
      width: 20px;
      height: 20px;
      font-size: 20px;
    }
  }

  &.drop-right {
    .dropdown-wrapper {
      width: fit-content;
      left: unset;
      right: 0;
    }
  }

  .dropdown-wrapper {
    width: fit-content;

    .dropdown {
      max-height: 200px;

      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 20px;
        gap: 32px;

        &:not(:last-child) {
          border-bottom: 1px solid pick-color('primary', 'fog');
        }

        .save-state {
          height: 20px;
          display: flex;
          justify-content: center;
          flex-grow: 1;
          cursor: pointer;
          font-size: 14px;
          color: pick-color('primary', 'ash');
          font-weight: 500;

          svg {
            width: 20px;
            height: 20px;
            stroke: pick-color('primary', 'blue');
          }
        }

        .save-delete {
          display: flex;
          cursor: pointer;

          svg {
            width: 20px;
            height: 20px;
            stroke: pick-color('secondary', 'red');
          }
        }
      }
    }
  }
}

.save-search-dialog-wrapper {
  padding: 20px 28px;

  .save-search {
    color: pick-color('primary', 'blue');

    &:hover {
      background-color: pick-color('primary', 'blue');
      color: white;
    }
  }
}
